import Vue from "vue";
import {Button} from "mint-ui"
import App from "./App.vue";
import router from "./router";
import store from "./store";
//*mock
import "./mock/mockServer"

//*Button
Vue.component(Button.name, Button);

var vm = new Vue({
  el: "#app",
  render: h => h(App),
  router,
  store
});
Vue.use(vm);



